import React, { Component } from 'react'
import Button from 'react-bootstrap/Button';
import { MDBInput } from "mdbreact"
import LazyLoad from 'react-lazyload'
import Loadable from 'react-loadable'
export default class MaintenanceTwo extends Component {
    constructor(props){
        super(props)
        this.state = {
            StepTwoFieldOne : '',
            StepTwoFieldTwo : ''
        }
    }

    onChange = e => {
        const { name, value } = e.target
        this.setState({
          [name]: value
        })
      }

      sendData(){
          console.log(" = " + this.state.StepTwoFieldOne)
          console.log(" = " + this.state.StepTwoFieldTwo)
      }

    render() {
        return (
        <div>
        <LazyLoad>
            <form>
                <MDBInput label="" name="StepTwoFieldOne" onChange={this.onChange}/>
                <h6></h6>
                <MDBInput label="" name="StepTwoFieldTwo" onChange={this.onChange}/>
            </form>
            <div className="container-fluid">
            <Button variant="btn btn-block btn-success" onClick={ () => this.sendData()}></Button>{' '}
            <br />
            </div>
        </LazyLoad>
        </div>
        )
    }
}
